.sign-page {
  position: fixed;
  height: 100%;
  width: 100%;
  background-color: #e3e0d0;
  background-size: cover;
}

.sign-r1 {
  position: fixed;
  height: 20%;
  width: 100%;
  background-color: #181817;
}

.sign-container {
  position: fixed;
  margin-left: auto;
  margin-right: auto;
  top: 20%;
  height: 60%;
  width: 100%;
  background-color: #181817;
}

.sign-r2 {
  position: fixed;
  top: 80%;
  height: 20%;
  width: 100%;
  background-color: #181817;
}

.sign-image-box {
  background-image: url("https://cdn.pixabay.com/photo/2014/04/02/19/06/quito-ecuador-308159_960_720.jpg");
  background-repeat: no-repeat;
  background-color: #e3e5f0;
  background-size: cover;
  background-position: center;
}

@media (min-width: 3600px) {
  .Grid-item {
    flex-basis: 66.666667%; /* Equivalente a md={7} */
  }
}

@media (min-width: 960px) {
  .Grid-item {
    flex-basis: 33.333333%; /* Equivalente a sm={4} */
  }
}

@media (min-width: 1280px) {
  .Grid-item {
    flex-basis: 0;
    flex-grow: 1; /* Equivalente a xs={false} */
  }
}

.sign-in-form {
  background-color: #cec9af;
  position: relative;
}

.only-form {
  position: absolute;
  width: 80%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
